<template>
	<view class="role_choose">
		<!-- 自定义导航 -->
		<u-navbar title="选择您的角色" @rightClick="rightClick" :autoBack="true" bgColor="rgba(255, 255, 255, 0.2)"
			titleStyle="color:#010101;font-size: 31rpx;">
			<view class="u-nav-slot" slot="left"
				style="width:57rpx;height:57rpx;text-align: center;line-height: 57rpx;border:1px solid #fff;border-radius: 50%;">

				<u-icon name="arrow-left" size="19" color="#fff"
					custom-style="display: inline-block;line-height: 57rpx;margin:0 auto;left:-2rpx;"></u-icon>
			</view>
		</u-navbar>
		<view class="cont" :style="{top:marginTop+tabMargin+'px',height:contHeight+'px'}">
			<view class="btns flex">
				<view class="btn">我要找工作</view>
				<view class="btn">我要招人</view>
			</view>
		</view>
	</view>
</template>

<script>
	const app = getApp()
	export default {
		data() {
			return {
				imgUrl: app.globalData.baseImageUrl,
				marginTop: app.globalData.marginTop,
				tabMargin: app.globalData.tabMargin,
				contHeight: 0,
				periodList: [{
						value: "month",
						text: "/月"
					},
					{
						value: "day",
						text: "/天"
					},
					{
						value: "hour",
						text: "/时"
					}
				],
				info: {
					create_time: "",
					intention_report: {
						overview: {
							total_score: 0,
							summary: ""
						},
						work_conditions: [],
						dimensions: []
					},
					job_name: "",
					worker_name: ""
				},
				id: "",
				list: [],
				currentPage: 1,
				pagination: {},
				chartData: {},
				opts: {
					color: ["#1677FF", "#1677FF", "#FAC858", "#EE6666", "#73C0DE", "#1677FF", "#FC8452", "#9A60B4",
						"#ea7ccc"
					],
					padding: undefined,
					title: {
						name: "80%",
						fontSize: "15",
						color: "#1677FF"
					},
					subtitle: {
						name: "匹配度",
						fontSize: "11",
						color: "#333333"
					},
					extra: {
						arcbar: {
							type: "circle",
							width: 4,
							backgroundColor: "#E9E9E9",
							startAngle: 1.5,
							endAngle: 0.25,
							gap: 2,
						}
					}
				},
			}
		},
		onLoad(param) {
			this.id = param.id
			uni.setNavigationBarColor({
				frontColor: '#000000',
				backgroundColor: 'transparent'
			})
			uni.hideShareMenu()
			this.contHeight = app.globalData.systemHeight - this.marginTop - this.tabMargin
			this.getInfo()
		},
		methods: {
			getInfo() {
				let url = "https://api.test.lingdongzhida.swiftwd.com/worker/" + this.id + "/intentionReport"
				uni.request({
					url: url,
					method: "GET",
					success(res) {
						if (res.code == 0) {
							this.info = res.data
							let chartData = {
								series: [{
									name: "匹配度",
									color: "#1677FF",
									data: res.data.overview.total_score
								}]
							};
							this.chartData = JSON.parse(JSON.stringify(chartData));
						} else {
							uni.showModal({
								title: res.msg
							})
						}
					}
				})
				// this.$request(url).then(res => {
				// 	if (res.code == 0) {
				// 		this.info = res.data
				// 		let chartData = {
				// 			series: [{
				// 				name: "匹配度",
				// 				color: "#1677FF",
				// 				data: res.data.overview.total_score
				// 			}]
				// 		};
				// 		this.chartData = JSON.parse(JSON.stringify(chartData));
				// 	} else {
				// 		uni.showModal({
				// 			title: res.msg
				// 		})
				// 	}
				// })
			},
			toZhida() {
				uni.navigateToMiniProgram({
					appId: 'wx3237f09d180aeedf',
					path: 'pages/index/index',
					success(res) {
						// 打开成功
					}
				})
			},
			toChat() {
				let _this = this
				// 获取页面栈
				let pages = getCurrentPages();
				// 上一个页面实例
				let prevPage = pages[pages.length - 2];
				// 调用上一个页面的方法
				uni.navigateBack()
				if (prevPage && prevPage.$vm && typeof prevPage.$vm.changeTab === 'function') {
					prevPage.$vm.changeTab(1);
				}
				setTimeout(function() {
					if (prevPage && prevPage.$vm && typeof prevPage.$vm.sendBtnMsg === 'function') {
						let obj = {
							type: "",
							msg: "我想了解一下和" + _this.info.job_name + "这个工作类似的其他职位。"
						}
						prevPage.$vm.sendBtnMsg(obj);
					}
				}, 1000)


			}
		}
	}
</script>

<style lang="scss">
	::v-deep {}

	.role_choose {
		height: 100vh;
		background: url($back-ground-url+"/worker/new/welcome_bg.png") no-repeat;
		background-size: cover;
		color: $base-fontcolor;

		.cont {
			position: fixed;
			left: 0;
			width: 100%;
			overflow-y: scroll;

			.btns {
				.btn {
					width: 212rpx;
					height: 77rpx;
					background: rgba(33, 111, 249, 0.19);
					box-shadow: 0rpx 2rpx 4rpx 0rpx rgba(0, 0, 0, 0.15);
					border-radius: 15rpx;
					border: 2rpx solid #216FF9;
					font-weight: 400;
					font-size: 29rpx;
					color: #1677FF;
					line-height: 77rpx;
					text-align: center;

					&:nth-child(2) {
						background: #216FF9;
						color: #FFFFFF;
					}
				}
			}

		}
	}
</style>